    left: 29px;
    padding: 15px 30px;
    position: absolute;
    top: -15px;
    width: 70px;
    -webkit-transition: opacity ease-in-out 0.3s;
    -o-transition: opacity ease-in-out 0.3s;
    transition: opacity ease-in-out 0.3s; }
  .tooltip:after {
    border: solid 7px transparent;
